import React from 'react'
import { SocialIcon } from 'react-social-icons';

function Socialmedia() {
    return (
        <div className="social-links">
            <SocialIcon  className = "socialBtn" bgColor = "#9C9C9C" url="https://github.com/notevenagoat/notevenagoat" /> <br />
            <SocialIcon  className = "socialBtn" bgColor = "#9C9C9C" url="https://www.linkedin.com/in/notevenagoat" /> <br />
            <SocialIcon  className = "socialBtn" bgColor = "#9C9C9C" url="https://www.instagram.com/notevenagoat/" /> <br />
        </div>
    )
}

export default Socialmedia
